.Login{
  form{
    width: 90%;
    max-width: 600px;
    margin: 2em auto 0
  }
  .input-group{
    $color: #999;
    $border: 1px solid $color;
    display: grid;
    grid-template-columns: 2em 1fr;
    border: $border;
    span{
      background: var(--color-second);
      color: rgba(#000,.5);
      display: flex;
      justify-content: center;
      align-items: center;
      border-right: $border;
    }
  }
  input{
    
  }
  
}
