.products, .admin-products_list{
  max-width: $max-width;
  margin: 2em auto;
  padding: 0 1em;
  @include from(m){
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 1em;
  }
  @include from(l){
    grid-template-columns: repeat(3,1fr);
    grid-gap: 2em;
  }
  &__item{
    cursor: pointer;
    position: relative;
    overflow: hidden;
/*
  800 = 100
  533
 */
    img{
      transition: all .3s;
    }
    &:hover{
      img{
        @include from($bp){
          transform: scale(1.05);
          filter: grayscale(90%);
        }
      }
    }
    @include to(m){
      margin-bottom: 1em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
}
.products__img-container{
  overflow: hidden;
  & > img{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
  }
}
