  position: relative;
  overflow: hidden;
}

.slider-container {
  display: flex;
  width: 200%;
  transition: transform .5s;
}

.slider-container.second {
  transform: translateX(-50%);
}

.slider-item {
  flex-basis: 50%;
  position: relative;
  height: auto;
  min-height: 300px;
  padding: 1em 1em 4em;
}

@media screen and (min-width: 1024px) {
  .slider-item {
    min-height: 600px;
  }
}

.slider-item picture {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.slider-item picture img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.slider-content, .form-container {
  position: relative;
  z-index: var(--z-banner);
  background: rgba(0, 0, 0, 0.5);
  padding: 1em;
  color: #fff;
  text-align: center;
}

@media screen and (min-width: 1024px) {
  .slider-content, .form-container {
    max-width: 500px;
    left: calc(100% - 500px - 10%);
    top: 5%;
    background: transparent;
  }
  .slider-content p, .form-container p {
    font-size: 1.2em;
  }
}

.slider-navigator {
  position: absolute;
  display: flex;
  bottom: 0;
  right: 0;
  padding: .5rem 1rem;
  cursor: pointer;
}

@media screen and (min-width: 1024px) {
