body{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
footer{
  margin-top: auto ;
}
.container{
  width: 100%;
  max-width: $max-width;
  margin-left: auto ;
  margin-right: auto;
  padding-left: $gutter;
  padding-right: $gutter;
  &.full{
    max-width: 100%;
  }
  &.no-padding{
    padding: 0;
  }
}
