.main-footer{
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-align: center;
}
.social-network{
  @extend %list-style;
  display: flex;
  justify-content: center;
  li:nth-child(2){
    margin: 0 1em;
  }
  a{
    font-size: 2em;
    color: var(--color-main);
    transition: color .3s;
    &:hover{
      color: var(--color-second);
    }
  }
}

.dev-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @include from(m){
    flex-direction: row;
  }
  .separator{
    margin-right: .4em;
    margin-left: .4em;
    @include showFrom(m);
  }
  a:hover{
    color: var(--color-second)
  }
}

.main-footer__copy{
  position: relative;
  top: .2em;
  margin-bottom: 0;
  @include from(m){
    position: static;
  }
}
