.slider{
  position: relative;
  overflow: hidden;
}
.slider-container{
  display: flex;
  width: 200%;
  transition: transform .5s;
  &.second{
    transform: translateX(-50%);
  }
}
.slider-item{
  flex-basis: 50%;
  position: relative;
  height: auto;
  min-height: 300px;
  padding: 1em 1em 4em;
  @include from($bp){
    min-height: 600px;
  }
  picture{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    img{
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
}
.slider-content, .form-container{
  position: relative;
  z-index: var(--z-banner);
  background: rgba(#000,.5);
  padding: 1em;
  color: #fff;
  text-align: center;
  @include from($bp){
    $width: 500px;
    max-width: $width;
    left: calc(100% - #{$width} - 10%);
    top: 5%;
    background: transparent;
    p{
      font-size: 1.2em;

    }
  }
}
.slider-navigator{
  position: absolute;
  display: flex;
  bottom: 0;
  right: 0;
  padding: .5rem 1rem;
  cursor: pointer;
  @include from($bp){
    right: 10%;
    bottom: 10%;
    transform: scale(1.3) translateX(-10%);
  }
}
.slider-arrows{
  font-size: 2em;
  margin-right: 1rem;
  &:last-child{
    margin-right: 0;
  }
  &::before{
    background: radial-gradient(#fff 50%, transparent 50%);
    border-radius: 50%;
  }
}
p.first-banner{
  @include from($bp){
    color: #222 !important;
  }
}
