[class*=icon-]::before{
  position: relative;
  top: .15rem;
}
.owl-nav{
  position: relative;
  z-index: var(--z-back);
  transform: translateY(-5px) !important;
  button, span{
    line-height: 1 !important;
    margin: 0 .4em 0 0;
  }
  span{
    font-size: 3em;
  }
}
.owl-carousel{
  @extend %list-style;
  position: relative;
}
