*,
*::before,
*:after {
  box-sizing: border-box;
}
*::selection{
  background: $color-second;
}

html, body {
  //  font-size: fontSize(small);
  margin: 0;
  line-height: var(--line-height);
  font-size: var(--font-size);

}

body{
  //overflow-x: hidden !important;
  line-height: var(--line-height);
  font-size: var(--font-size);
}

img{
  display: inline-block;
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  line-height: var(--line-height);
  font-size: var(--font-size);
}

p {
  &:last-child, &:nth-child(only){
    margin-bottom: .5em;
  }
}

a {
  color: inherit;
}
figure {
  padding: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
}
p{
  margin-bottom: .5em;
  &:only-child, &:only-of-type, &:last-child, &:last-of-type {
    margin-bottom: 0;
  }
}
button{
  &:active, &:focus{
    outline: none !important;
  }
}
