.banner {
  min-height: 500px;
  position: relative;
  padding: 1em;
  &-image {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: var(--z-back);
    img{
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
}
