.banner-page{
  max-width: 100%;
  min-height: 500px;
  position: relative;
  padding: 1em;
  display: flex;
  justify-content: center;
  @include from($bp){
    justify-content: flex-end;
    padding: 2em 10% 2em 0 ;
  }
  &-image {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: var(--z-back);
    img{
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
  > .form-container-page{
    background: rgba(#000,.4);
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #fff;
    padding: 1em;
    p{
      font-size: 1em;
    }
    @include from($bp){
      $width: 500px;
      padding: 1em 2em;
      max-width: $width;
      //left: calc(100% - #{$width} - 10%);
      //top: 5%;
      //background: transparent;
      & > *{
        margin-bottom: 1em;
        &:last-child{
          margin-bottom: 0;
        }
      }

    }
    h2,h3,p{
      text-align: center;
    }
    a{
      color: #fff;
    }
  }
}

