// Typography
$font-family: 'sans-serif';
$bp: l;

:root{
  --input-height: 2.6rem;
  --header-height: 9rem;
  --footer-height: 6rem;
  --z-back    : -10;
  --z-normal  : 1;
  --z-tooltip : 10;
  --z-banner: 75;
  --z-banner-c: 90;
  --z-fixed   : 100;
  --z-modal   : 1000;
  @include from(l){
    --header-height: 15rem;
  }
}


:root {
  --main-font: 'Raleway';
  --alternate-font: sans-serif;
  --font-size: 16px;
  --line-height: 1.6;
  
  --r: 0;
  --g: 0;
  --b: 0;
  --a: 1;
  // 0° 0% 4%

  --main-color-h: 0;
  --main-color-s: 0%;
  --main-color-l: 4%;
  --main-color-a: 1;


  --main-color: hsla(var(--main-color-h), var(--main-color-s), var(--main-color-l), var(--main-color-a));
  --main-modal-color: rgb(217, 0, 98, .75);
  --second-color: rgb(20, 25, 45);
  --second-modal-color: rgba(20, 25, 45, .75);
  --alternate-color: rgb(80, 20, 100);
  --alternate-modal-color: rgb(80, 20, 100, .75);
  --action-color: rgb(84, 194, 141);
  --action-modal-color: rgba(84, 194, 84, .75);

  --bg-color: rgb(242, 244, 247);
  --bg-modal-color: rgba(242, 244, 247, .75);
  --bg-alternate-color: rgb(255, 255, 255);
  --bg-alternate-modal-color: rgba(255, 255, 255, .75);
  --border-color: #DDD;

  --text-color: #515B6D;
  --title-color: #333;

  --link-color: #509EE3;
  --link-hover-color: var(--main-color);

  --max-width: 1200px;
  --header-height: 5rem;

  --logo-width: 4rem;
  --logo-height: 4rem;
  --logo-spacing: .5rem;

  --nav-height: 64px;
}
