.modal{
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(#000,.9);
  width: 100%;
  height: 100vh;
  z-index: var(--z-modal);
  opacity: 0;
  animation: showModal .3s  linear forwards;
  @keyframes showModal {
    to {
      opacity: 1;
    }
  }
  &__container {
    width: 90%;
    max-width: 800px;
    padding: 2em;
    background: #fff;
  }
  &__close{
    position: absolute;
    right: 1em;
    top: 1em;
    color: #fff;
    font-size: 3em;
  }
}


