.main-header{
  position: relative;
  z-index: var(--z-fixed);
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include from($bp){
    flex-direction: column;
  }
}
.logo-container{
  height: var(--header-height);
  display: flex;
  align-items: center;
  @include from($bp){
    padding-top: 1em;
    align-items: flex-start;
    max-height: calc(var(--header-height) - 2em - 1em);
  }
  img {
    display: block;
    height: 100%;
    max-height: calc(var(--header-height) - 2em);
    width: auto;
    @include from($bp){
      max-height: calc(var(--header-height) - 2em - 3em);
    }
  }
}
.main-menu{
  $border: 1px solid rgba(#fff,.2);
  background: $color-second;
  position: absolute;
  left: 0;
  top: var(--header-height);
  width: 100%;
  @include edMenu($bp);
  overflow: hidden;
  color: #fff;
  max-height: 0;
  transition: max-height .3s;
  &.show{
    max-height: calc(3em * 5);
  }
  @include from($bp) {
    max-height: 3em;
    position: static;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    background: transparent;
    color: var(--color-font);
    li{
      border: none;
      transition: all .3s;
      &.selected, &:hover{
        background: var(--color-main);
        color: #fff
      }
      &:last-child{
        position: relative;
        top: -1px;
      }
    }
  }
  li{
    border-bottom: $border;
    &:last-child{
      border-bottom: none;
    }
  }
  a{
    text-transform: uppercase;
  }
}
.toggle-menu{
  @include showTo($bp);
  font-size: 2.5em;
  color: var(--color-main);
  cursor: pointer;
}

