.form-container{
  text-align: left;
  h2{
    margin-bottom: 1rem;
  }
  @include from($bp){
    h2, label{
      color: #333;
    }
    label{
      font-weight: 700;
    }
  }
}
.input-group{
  $pd: .4rem;
  text-align: inherit;
  margin-bottom: 1rem;
  @include from($bp){
    //display: grid;
    //grid-template-columns: 170px 1fr;
    label, input, textarea{

    }
  }
  label,input, textarea{
    display: block;
    text-align: left;
    font-family: var(--font-main);
    font-size: 1rem;
  }
  label, input{
    line-height: 2em;
  }
  input, textarea{
    padding-left: $pd;
    padding-right: $pd;
    border: none;
    width: 100%;
    &:focus{
      outline-color: $color-second;
    }
  }
  textarea{
    padding-top: $pd;
    resize: none;
    height: 6rem;
  }
}
.btn{
  background: $color-second;
  color: #333;
  font-weight: 700;
  margin: 0;
  &:hover{
    background: darken($color-second,10);
    color: #fff;
  }
}
.contact__form-footer{
  display: grid;
  grid-template-columns: 1fr 120px;
  grid-gap: 1rem;
  height: 42.6px;
}
.btn-send{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 42.6px;
  &.disabled{
    background: #ccc;
    color: darkgreen;
    cursor: initial;
  }
}
.loader{
  display: none;
  margin-right: .5em;
  width: 2em;
  height: 2em;
  &.show{
    display: block;
  }
}

.section-msg.show{
  &.show{
    position: absolute;
    background: rgba(#000,.5);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    padding: 1em;
    opacity: 0;
    animation: opacity .3s .1s forwards;
  }
  @keyframes opacity {
    to{
      opacity: 1;
    }
  }
}
.messagges-text{
  text-align: center;
  padding: .5em;
  color: rgba(#fff,.8);
  &.success{
    background: green;
  }
  &.error{
    background: darkred;
  }
}
.contact__form{
  position: relative;
}
